import axios from '../../Assets/axiosconfig';
import { useState } from 'react';
import { Form, Container, Button } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { loginRequest, loginSuccess, loginFaild } from '../../redux/auth';


function Login() {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const dispatch = useDispatch();


    const handleLogin = async (e) => {
        e.preventDefault();
        dispatch(loginRequest());
        try {
            const res = await axios.post('/users/login', {
                username, password
            });
            dispatch(loginSuccess(res.data));

        } catch (error) {
            dispatch(loginFaild(error));
            console.log(error);
        }
    };

    return (
        <div>
            <div className="min-vh-100 mt-5 ">
                <Container className="" >
                    <div className="form-signin  align-items-center justify-content-center d-flex">
                        <Form >
                            <Form.Group className="mb-3" controlId="formBasicEmail">
                                <Form.Label>Email address</Form.Label>
                                <Form.Control type="email" placeholder="Enter email" onChange={e => setUsername(e.target.value)} />
                                <Form.Text className="text-muted">
                                    We'll never share your email with anyone else.
                                </Form.Text>
                            </Form.Group>

                            <Form.Group className="mb-3" controlId="formBasicPassword">
                                <Form.Label>Password</Form.Label>
                                <Form.Control type="password" placeholder="Password" onChange={e => setPassword(e.target.value)} />
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="formBasicCheckbox">
                                <Form.Check type="checkbox" label="Log me in" />
                            </Form.Group>
                            <div>
                                <Button variant="primary" type="submit" className='me-5' onClick={handleLogin}>
                                    Submit
                                </Button>
                                <NavLink to='/signin1'>Register</NavLink>
                            </div>
                        </Form>
                    </div>
                </Container>
            </div >
        </div >
    )
}

export default Login
